import React, { useState } from 'react'
import { TextField, Button } from '@material-ui/core'
import { withRouter } from 'react-router-dom'
import axios from 'axios'

const inputStyle = {
  width: '100%',
}

const style = {
  width: '30%',
  margin: 'auto',
}

function Add({ history }) {
  let name = useInputForm()
  let email = useInputForm()
  let phone = useInputForm()

  let returnBtn = (e) => {
    e.preventDefault()

    history.push('/')
  }

  let addBtn = (e) => {
    e.preventDefault()

    axios
      .post('http://localhost:8080/api/v1/employees', {
        name: name.value,
        email: email.value,
        phone: phone.value,
      })
      .then(() => {
        history.push('/')
      })
  }

  return (
    <div style={style}>
      <h1 style={{ textAlign: 'center' }}>Add New Employee</h1>
      <form>
        <TextField {...name} style={inputStyle} id='name' label='Name' />
        <TextField {...email} style={inputStyle} id='email' label='Email' />
        <TextField {...phone} style={inputStyle} id='phone' label='Phone' />
        <Button
          onClick={addBtn}
          style={{ marginTop: '10px', marginRight: '10px' }}
          variant='contained'>
          Add
        </Button>
        <Button
          onClick={returnBtn}
          style={{ marginTop: '10px' }}
          variant='contained'>
          Return
        </Button>
      </form>
    </div>
  )
}

let useInputForm = () => {
  const [input, setInput] = useState('')

  function handleChange(e) {
    setInput(e.target.value)
  }

  return {
    value: input,
    onChange: handleChange,
  }
}

export default withRouter(Add)
